@mixin button() {
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
